import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import styles from './MultiplayerChoice.module.css';

axios.defaults.withCredentials = true;

function MultiplayerChoice() {
  const [lobbyCode, setLobbyCode] = useState('');
  const navigate = useNavigate();

  const handleCreateLobby = async () => {
    try {
      const response = await axios.post('https://music-game-backend-production.up.railway.app/api/lobby/create');
      const newLobbyCode = response.data.code;
      navigate(`/lobby/${newLobbyCode}`);
    } catch (error) {
      console.error('Error creating lobby:', error);
    }
  };

  const handleJoinLobby = async () => {
    if (lobbyCode.trim() !== '') {
      try {
        const response = await axios.post('https://music-game-backend-production.up.railway.app/api/lobby/join', { code: lobbyCode });
        if (response.data.code === lobbyCode) {
          navigate(`/lobby/${lobbyCode}`);
        } else {
          alert('Invalid lobby code. Please try again.');
        }
      } catch (error) {
        console.error('Error joining lobby:', error);
        alert('Error joining lobby. Please try again.');
      }
    } else {
      alert('Please enter a valid lobby code.');
    }
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Multiplayer Mode</h1>
      <div className={styles.choiceContainer}>
        <div className={styles.joinContainer}>
          <input
            type="text"
            placeholder="Enter Lobby Code"
            value={lobbyCode}
            onChange={(e) => setLobbyCode(e.target.value)}
            className={styles.input}
          />
          <button onClick={handleJoinLobby} className={styles.button}>Join Lobby</button>
        </div>
        <button onClick={handleCreateLobby} className={`${styles.button} ${styles.createButton}`}>Create Lobby</button>
      </div>
      <button onClick={() => navigate('/')} className={styles.backButton}>
        Go Back
      </button>
    </div>
  );
}

export default MultiplayerChoice;
