import React, { useState, useEffect, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import io from 'socket.io-client';
import axios from 'axios';
import styles from './MultiplayerGame.module.css';

axios.defaults.withCredentials = true;

function MultiplayerGame() {
    const { lobbyCode } = useParams();
    const navigate = useNavigate();
    const socketRef = useRef(null);

    // Game state variables
    const [phase, setPhase] = useState('waiting'); // waiting, roundStarted, roundEnded, transition, gameEnded
    const [round, setRound] = useState(0);
    const [timer, setTimer] = useState(0);
    const [song, setSong] = useState(null);
    const [userGuess, setUserGuess] = useState('');
    const [guessSubmitted, setGuessSubmitted] = useState(false);
    // We'll store the personalized result from the backend here:
    const [personalResult, setPersonalResult] = useState(null);
    const [gameResult, setGameResult] = useState(null);
    const [currentUserId, setCurrentUserId] = useState(null);

    const timerIntervalRef = useRef(null);

    // Retrieve current user id from session
    useEffect(() => {
        axios.get('https://music-game-backend-production.up.railway.app/music-backend/api/init-session')
            .then(response => {
                setCurrentUserId(response.data.user_id);
            })
            .catch(error => {
                console.error('Error initializing session:', error);
            });
    }, []);

    // Connect to the Socket.IO server and set up event listeners.
    useEffect(() => {
        // Force websocket transport with polling fallback.
        const socket = io('https://music-game-backend-production.up.railway.app', { 
            transports: ['websocket', 'polling']
          });
        socketRef.current = socket;
        socket.emit('join', { lobby_code: lobbyCode });

        socket.on('round_started', (data) => {
            setPhase('roundStarted');
            setRound(data.round);
            setSong(data.song);
            setUserGuess('');
            setGuessSubmitted(false);
            setTimer(data.duration); // should be 30 seconds
        });

        // Listen for the personalized round result event.
        socket.on('round_ended_personal', (data) => {
            setPhase('roundEnded');
            setPersonalResult(data); // data contains round, correct_answer, and guess_result for this user
            clearInterval(timerIntervalRef.current);
        });

        socket.on('round_transition', (data) => {
            setPhase('transition');
            setTimer(data.duration); // should be 5 seconds
        });

        socket.on('game_ended', (data) => {
            setPhase('gameEnded');
            setGameResult(data);
            clearInterval(timerIntervalRef.current);
        });

        return () => {
            if (socketRef.current) {
                socketRef.current.emit('leave', { lobby_code: lobbyCode });
                socketRef.current.disconnect();
            }
            clearInterval(timerIntervalRef.current);
        };
    }, [lobbyCode]);

    // Timer effect: update the countdown every second when timer > 0.
    useEffect(() => {
        if (timer > 0) {
            timerIntervalRef.current = setInterval(() => {
                setTimer(prev => {
                    if (prev <= 1) {
                        clearInterval(timerIntervalRef.current);
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);
        }
        return () => clearInterval(timerIntervalRef.current);
    }, [timer]);

    // Handle guess submission (only one try per round).
    const handleSubmitGuess = async (e) => {
        e.preventDefault();
        if (guessSubmitted || userGuess.trim() === '') return;
        try {
            await axios.post('https://music-game-backend-production.up.railway.app/music-backend/api/submit-guess', { guess: userGuess, lobby_code: lobbyCode });
            setGuessSubmitted(true);
        } catch (error) {
            console.error('Error submitting guess:', error);
            alert('Error submitting guess.');
        }
    };

    // Render content based on the current phase.
    const renderContent = () => {
        switch (phase) {
            case 'waiting':
                return <div className={styles.waiting}>Waiting for the game to start...</div>;
            case 'roundStarted':
                return (
                    <div className={styles.round}>
                        <h2>Round {round}</h2>
                        {song && (
                            <div className={styles.songInfo}>
                                <p>Artist: {song.artist}</p>
                                {/* An audio player to play the song preview */}
                                <audio controls src={song.audio_url} />
                            </div>
                        )}
                        <p>Time remaining: {timer} seconds</p>
                        {!guessSubmitted ? (
                            <form onSubmit={handleSubmitGuess} className={styles.guessForm}>
                                <input
                                    type="text"
                                    placeholder="Enter your guess"
                                    value={userGuess}
                                    onChange={(e) => setUserGuess(e.target.value)}
                                    className={styles.guessInput}
                                />
                                <button type="submit" className={styles.submitButton}>Submit Guess</button>
                            </form>
                        ) : (
                            <p>Your guess has been submitted.</p>
                        )}
                    </div>
                );
            case 'roundEnded':
                return (
                    <div className={styles.roundEnded}>
                        <h2>Round {round} Results</h2>
                        {personalResult && (
                            <div>
                                <p>Correct Answer: {personalResult.correct_answer}</p>
                                {personalResult.guess_result ? (
                                    personalResult.guess_result.correct ? (
                                        <p>You guessed correctly!</p>
                                    ) : (
                                        <p>You guessed incorrectly. Your guess: {personalResult.guess_result.guess}</p>
                                    )
                                ) : (
                                    <p>You did not submit a guess.</p>
                                )}
                            </div>
                        )}
                        <p>Transitioning to next round...</p>
                    </div>
                );
            case 'transition':
                return (
                    <div className={styles.transition}>
                        <h2>Get ready for the next round...</h2>
                        <p className={styles.timer}>Starting in {timer} seconds</p>
                        {personalResult && (
                            <div className={styles.resultCard}>
                                <p className={styles.resultTitle}>Previous Round Results:</p>
                                <p className={styles.correctAnswer}>
                                    Correct Answer: {personalResult.correct_answer}
                                </p>
                                {personalResult.guess_result ? (
                                    personalResult.guess_result.correct ? (
                                        <p className={styles.correctGuess}>You guessed correctly!</p>
                                    ) : (
                                        <p className={styles.incorrectGuess}>
                                            You guessed incorrectly. Your guess: {personalResult.guess_result.guess}
                                        </p>
                                    )
                                ) : (
                                    <p className={styles.noGuess}>You did not submit a guess.</p>
                                )}
                            </div>
                        )}
                    </div>
                );
            case 'gameEnded':
                return (
                    <div className={styles.gameEnded}>
                        <h2>Game Over</h2>
                        {gameResult && (
                            <div>
                                <p>Winner: {gameResult.winner}</p>
                                <h3>Scores:</h3>
                                <ul>
                                    {gameResult.players.map(player => (
                                        <li key={player.id}>
                                            Player {player.id}: {player.score}
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        )}
                        <button onClick={() => navigate('/')} className={styles.homeButton}>Return Home</button>
                    </div>
                );
            default:
                return null;
        }
    };

    return (
        <div className={styles.container}>
            <h1 className={styles.title}>Multiplayer Game - Lobby: {lobbyCode}</h1>
            {renderContent()}
        </div>
    );
}

export default MultiplayerGame;
