import React, { useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import Home from './components/Home';
import SinglePlayer from './components/SinglePlayer';
import MultiplayerChoice from './components/MultiplayerChoice';
import MultiplayerLobby from './components/MultiplayerLobby';
import MultiplayerGame from './components/MultiplayerGame';
import './App.css';
import axios from 'axios';

axios.defaults.withCredentials = true;

function App() {
  useEffect(() => {
    initSession();
  }, []);

  const initSession = async () => {
    try {
      await axios.get('https://music-game-backend-production.up.railway.app/api/init-session');
    } catch (error) {
      console.error('Error initializing session:', error);
    }
  };

  return (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/multiplayer" element={<Home />} />
      <Route path="/singleplayer" element={<SinglePlayer />} />
      <Route path="/multiplayer-choice" element={<MultiplayerChoice />} />
      <Route path="/lobby/:lobbyCode" element={<MultiplayerLobby />} />
      <Route path="/game/:lobbyCode" element={<MultiplayerGame />} />
      <Route path="/game/:lobbyCode/:round" element={<MultiplayerGame />} />
      <Route path="/game/:lobbyCode/:round/:songId" element={<MultiplayerGame />} />
    </Routes>
  );
}

export default App;