import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import styles from './SinglePlayer.module.css';

// add cookies
axios.defaults.withCredentials = true;

function SinglePlayer() {
  const [songData, setSongData] = useState(null);
  const [guess, setGuess] = useState('');
  const [message, setMessage] = useState('');
  const [score, setScore] = useState(0);
  const navigate = useNavigate();

  useEffect(() => {
    resetScore();
    fetchRandomSong();
  }, []);

  const resetScore = async () => {
    try {
      const response = await axios.post('https://music-game-backend-production.up.railway.app/api/reset-score');
      setScore(response.data.score);
    } catch (error) {
      console.error('Error resetting score:', error);
    }
  };

  const fetchRandomSong = async () => {
    try {
      const response = await axios.get('https://music-game-backend-production.up.railway.app/api/random-song');
      setSongData(response.data);
      setMessage('');
      setGuess('');
    } catch (error) {
      console.error('Error fetching song:', error);
      setMessage('Failed to fetch song. Please try again.');
    }
  };

  const submitGuess = async () => {
    try {
      const response = await axios.post('https://music-game-backend-production.up.railway.app/api/validate-guess', {
        guess,
        song: songData.song
      });
      setMessage(response.data.message);
      setScore(response.data.score);
      if (response.data.correct) {
        fetchRandomSong();
      }
    } catch (error) {
      console.error('Error validating guess:', error);
      setMessage('Failed to validate guess. Please try again.');
    }
  };

  const goBack = () => {
    navigate('/');
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Single Player Mode</h1>
      <p className={styles.score}>Score: {score}</p>
      {songData && (
        <div className={styles.songContainer}>
          <audio controls src={songData.audio_url} className={styles.audioPlayer} />
          <input
            type="text"
            placeholder="Guess the song title"
            value={guess}
            onChange={(e) => setGuess(e.target.value)}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                submitGuess();
              }
            }}
            className={styles.input}
          />
          <button onClick={submitGuess} className={styles.button}>Submit Guess</button>
          <button onClick={fetchRandomSong} className={styles.button}>Skip song</button>
        </div>
      )}
      {message && <p className={styles.message}>{message}</p>}
      <button onClick={goBack} className={styles.go_back_button}>Go Back</button>
    </div>
  );
}

export default SinglePlayer;
