import React, { useState, useEffect, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import styles from './MultiplayerLobby.module.css';
import io from 'socket.io-client';

axios.defaults.withCredentials = true;

function Lobby() {
    const { lobbyCode } = useParams();
    const navigate = useNavigate();
    const [players, setPlayers] = useState([]);
    const [currentUserId, setCurrentUserId] = useState(null);
    const [isHost, setIsHost] = useState(false);
    const socketRef = useRef(null);

    // 1. Retrieve current session's user ID.
    useEffect(() => {
        axios.get('https://music-game-backend-production.up.railway.app/music-backend/api/init-session')
            .then(response => {
                setCurrentUserId(response.data.user_id);
            })
            .catch(error => {
                console.error('Error initializing session:', error);
            });
    }, []);

    // 2. Fetch the current lobby state from REST API.
    useEffect(() => {
        if (!currentUserId) return; // wait for user id
        axios.get(`https://music-game-backend-production.up.railway.app/music-backend/api/lobby/${lobbyCode}`)
            .then(response => {
                const lobby = response.data;
                if (lobby && lobby.players) {
                    setPlayers(lobby.players);
                    // Determine if current user is the host.
                    const currentUser = lobby.players.find(p => p.id === currentUserId);
                    setIsHost(currentUser ? currentUser.is_host : false);
                }
            })
            .catch(error => {
                console.error('Error fetching lobby data:', error);
            });
    }, [lobbyCode, currentUserId]);

    // 3. Set up Socket.IO for real-time updates.
    useEffect(() => {
        const socket = io('https://music-game-backend-production.up.railway.app', { 
            transports: ['websocket', 'polling']
          });        
        socketRef.current = socket;
        socket.emit('join', { lobby_code: lobbyCode });

        // Listen for new players joining.
        socket.on('player_joined', (data) => {
            console.log('Player joined:', data);
            setPlayers((prevPlayers) => {
                if (prevPlayers.some((p) => p.id === data.player_id)) return prevPlayers;
                return [...prevPlayers, { id: data.player_id, is_host: false }];
            });
        });

        // Listen for players leaving.
        socket.on('player_left', (data) => {
            console.log('Player left:', data);
            setPlayers((prevPlayers) => prevPlayers.filter((p) => p.id !== data.player_id));

            // Re-fetch lobby data to update host status if needed.
            if (currentUserId) {
                axios.get(`https://music-game-backend-production.up.railway.app/music-backend/api/lobby/${lobbyCode}`)
                    .then(response => {
                        const lobby = response.data;
                        if (lobby && lobby.players) {
                            const currentUser = lobby.players.find(p => p.id === currentUserId);
                            setIsHost(currentUser ? currentUser.is_host : false);
                        }
                    })
                    .catch(error => {
                        console.error('Error fetching lobby data:', error);
                    });
            }
        });

        // Listen for game start event
        socket.on('game_started', () => {
            console.log('Game has started!');
            navigate(`/game/${lobbyCode}`);
        });

        return () => {
            socket.emit('leave', { lobby_code: lobbyCode });
            socket.disconnect();
        };
    }, [lobbyCode, currentUserId, navigate]);

    // 4. Function to leave the lobby.
    const handleLeaveLobby = async () => {
        try {
            await axios.post('https://music-game-backend-production.up.railway.app/music-backend/api/lobby/leave', { code: lobbyCode });
            if (socketRef.current) {
                socketRef.current.emit('leave', { lobby_code: lobbyCode });
                socketRef.current.disconnect();
            }
            navigate('/');
        } catch (error) {
            console.error('Error leaving lobby:', error);
            alert('Error leaving lobby, please try again.');
        }
    };

    // Function to start the game (only available to host)
    const handleStartGame = async () => {
        try {
            const response = await axios.post('https://music-game-backend-production.up.railway.app/music-backend/api/lobby/start', { code: lobbyCode });
            if (response.data.success) {
                console.log('Game started by host');
                // Host may also navigate immediately, though the "game_started" event will also trigger navigation on all clients.
                navigate(`/game/${lobbyCode}`);
            } else {
                alert('Error starting game');
            }
        } catch (error) {
            console.error('Error starting game:', error);
            alert('Error starting game, please try again.');
        }
    };

    return (
        <div className={styles.container}>
            <h1 className={styles.title}>Lobby: {lobbyCode}</h1>
            <div className={styles.buttonGroup}>
                <button onClick={handleLeaveLobby} className={styles.leaveButton}>
                    Leave Lobby
                </button>
            </div>
            <ul className={styles.playerList}>
                {players.map((player) => (
                    <li key={player.id} className={styles.playerItem}>
                        {`Player ${player.id}`} {player.is_host ? '(Host)' : ''}
                    </li>
                ))}
            </ul>
            {isHost && (
                <button onClick={handleStartGame} className={styles.startButton}>
                    Start Game
                </button>
            )}
        </div>
    );
}

export default Lobby;
