import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import styles from './Home.module.css';

function Home() {
  const [joinCode, setJoinCode] = useState('');
  const navigate = useNavigate();


  const startSinglePlayer = () => {
    navigate('/singleplayer');
  };

  const navigateMultiPlayer = () => {
    navigate('/multiplayer-choice');
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Welcome to the Music Guess Game</h1>
      <button className={styles.button} onClick={startSinglePlayer}>Single Player</button>
      <button className={styles.button} onClick={navigateMultiPlayer}>Multiplayer</button>
    </div>
  );
}

export default Home;